export interface INotificationsProps {
  notificationsRepository: INotificationsRepository;
  mobile?: string;
}

export interface INotificationsRepository {
  findNotification(mobile): Promise<any>;
}

export class NotificationsRequests {
  private mobile?: string;

  private notificationsRepository: INotificationsRepository;

  constructor(protected props: INotificationsProps) {
    const { notificationsRepository, mobile } = props;
    this.mobile = mobile;
    this.notificationsRepository = notificationsRepository;
  }

  public async getVerificationCode() {
    return this.notificationsRepository.findNotification(this.mobile);
  }
}
